@import url('https://fonts.googleapis.com/css2?family=Rubik:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Libre+Franklin:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,300;1,400;1,500;1,700&display=swap');

h1, h2, h3, h4{
  font-family: 'Rubik', sans-serif;
  margin: 0;
}

p, a, span, h5, h6, li{
  font-family: 'Libre Franklin', sans-serif;
  margin: 0;
}

body, body > *{
  margin: 0;
  padding: 0;
}

.container{
  max-width: 1350px;
  margin: auto;
  padding: 0px 25px;
}